.container {
  max-width: 1270px;
  padding: 0 15px;
  margin: 0 auto;
}

.movie__info {
  position: relative;
}

.movie__background {
  position: absolute;
  width: 100%;
  height: 100vh;
}

.movie__background img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.movie__background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(rgba(0, 0, 0, 0.6)),
      to(rgba(0, 0, 0, 0.6))
    ),
    -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.6)), to(transparent));
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.6) 100%),
    linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, transparent 100%);
  z-index: 2;
}

.movie__content {
  display: flex;
  position: absolute;
  z-index: 3;
}

.movie__pic img {
  margin-top: 50%;
}

.movie__description {
  max-width: 800px;
  margin: 12% 0 0 48px;
}

.movie__name {
  color: #fff;
  font-size: 58px;
  margin-bottom: 24px;
  font-weight: bold;
}

.movie__name {
  color: #ffffff;
}

.movie__genre {
  color: #ffffff;
  margin-bottom: 24px;
}

.movie__rating {
  color: #ffffff;
  margin-bottom: 24px;
}

.movie__overview {
  color: #ffffff;
  line-height: 26px;
}

.btn__info {
  position: absolute;
  z-index: 10;
  margin-top: 74px;
  transition: 0.2s linear;
}

.btn__back {
  background: #ef0000;
  border: none;
  color: #fff;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.btn__info:hover {
  filter: brightness(1.5);
}

/* MEDIA */

@media screen and (max-width: 650px) {
  .movie__content {
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    z-index: 3;
  }

  .movie__description {
    margin: 0;
  }

  .movie__name {
    font-size: 48px;
  }
}

@media screen and (max-width: 460px) {
  .movie__name {
    font-size: 36px;
  }

  .btn__info {
    z-index: 9;
  }
}
